import React from 'react'
import HSLColorPicker from './hsl/hsl-colorpicker'
import Colr from 'colr'


var rgb = React.PropTypes.shape({
    r: React.PropTypes.number.isRequired,
    g: React.PropTypes.number.isRequired,
    b: React.PropTypes.number.isRequired
}).isRequired

let hsl = HSLColorPicker.propTypes.value


let hsl2Rgb = c => Colr.fromHslObject(c).toRawRgbObject(),
    rgb2Hsl = c => Colr.fromRgbObject(c).toRawHslObject()

class ColorPicker extends React.Component {

    render(){

        let { value, onChange } = this.props
        let isRGB = rgb( this.props, 'value' ) === null
        let inHsl = isRGB ? rgb2Hsl( value ) : value

        return <HSLColorPicker {...this.props} value={ inHsl } onChange={ outHSL => onChange( isRGB ? hsl2Rgb(outHSL) : outHSL )} />

    }
}

ColorPicker.propTypes = {

    /**
     *  An object representing a color.
     */
    value: React.PropTypes.oneOf([rgb, hsl]).isRequired,

    /**
     *  A callback triggered when the color is updated
     */
    onChange: React.PropTypes.func
}


ColorPicker.defaultProps = {
    value:{ r: 1, g: 0, b: 0 },
    onChange: a => a
}

export default ColorPicker
